body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.side-bar {
  position: fixed;
  background-color: #f7f7f7;
  width: 175px;
  height: 100%;
  z-index: 1;
  top:0;
  right: 0;
}

.compare-btn {
  position: absolute;
  bottom: 20px;
  right: 30px;
}

.comp-list-item {
  display: flex;
  align-items: center;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: lowercase;
  margin: 10px auto;
  padding: 0 5px;
  width: 140px;
  height: 160px;
  background-color: #F77065;
  border-radius: 5px;
  box-shadow: 2px 2px 6px #888888;
}
.body {
  height: 100vh;
  width: 100%;
  background-color: #f8f6ac;
}

.display {
  width: 40%;
  max-width: 300px;
  height: 350px;
  margin: 50px auto;
}

#versus {
  font-family: 'Pacifico', cursive;
}

.link {
  position: fixed;
  right: 0;
  bottom: 0;
  color: #1f1a1a;
}
body {
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-image: -webkit-linear-gradient(#adf4e6, #666);
  background-image: linear-gradient(#adf4e6, #666);
}
.App-header {
  min-height: 40vh;
  font-size: calc(10px + 2vmin);
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-family: 'Pacifico', cursive;
  color: 1F1a1a;
}

.searchfield {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

#search {
  margin: 10px;
  width: 50vw;
}

.search-btn {
  background-color: #707070;
  border: solid #707070 1px;
  color: #f7f7f7;
}

.list-group {
  margin: 15px auto;
  width: 80%;
}

.list-group-item {
  margin: 5px 0;
  padding-top: 3px;
  padding-bottom: 3px; 
}
